/* azuloscuro: #378DCA
azulclaro: #00BDEF */

.boxRegistre {
    text-align: center;
    padding: 10px;
    box-shadow: 0 3px 8px 0 #dad9d9;
    border-radius: 30px;
    display: block;
    width: 350px;
    margin: 0 10px;
    background: white;
}

.boxRegistre:hover,
.boxRegistre:focus {
    border-style: none;
    outline   : none;
    box-shadow: 0 3px 8px 0 #378dcac7;
}

.buttonRegistre {
    font-family: 'Lato', sans-serif;
    font-size: 1rem;
    background: none;
    border-radius: 30px;
    color: #1DAEFF;
    border-style: none;
    padding: 10px 15px;
    margin: 20px 0 10px 0;
    font-weight: 800;
}


@media screen and (max-width: 768px) {
    
    .boxRegistre {
        width: 35vw;
    }

    .boxRegistre img {
        width: 30vw;
    }
}

@media screen and (max-width: 425px) {
    
    .boxRegistre {
        margin: 10px 0;
        width: 60vw;
    }

    .boxRegistre img {
        width: 50vw;
    }

    .buttonRegistre {
        font-size: .9rem;
    }
}

