  @-webkit-keyframes modalIn {
    0% {
      transform: translateY(-3000px);
    }
    60% {
      transform: translateY(25px);
    }
    75% {
      transform: translateY(-10px);
    }
    90% {
      transform: translateY(5px);
    }
    100% {
      transform: translateY(0px);
    }
  }
  @keyframes modalIn {
    0% {
      transform: translateY(-3000px);
    }
    60% {
      transform: translateY(25px);
    }
    75% {
      transform: translateY(-10px);
    }
    90% {
      transform: translateY(5px);
    }
    100% {
      transform: translateY(0px);
    }
  }
  
  /*@keyframes animationOut {*/
  @-webkit-keyframes modalOut {
    0% {
      transform: translateY(5px);
