    0% {
      opacity: 0;
      display: none;
    }

    100% {
      opacity: 1;
      display: block;
    }
  }

  @-webkit-keyframes fadeInT {
    0% {
      opacity: 0;
      display: none;
    }

    50% {
        opacity: 0;
    }
  
    100% {
      opacity: 1;
      display: block;
    }
  }

  @keyframes fadeInT {
    0% {
      opacity: 0;
      display: none;
    }

    50% {
        opacity: 0;
    }
  
    100% {
      opacity: 1;
      display: block;
